content: center;\r\n      align-items: center;\r\n      background: black;\r\n      box-sizing: border-box;\r\n      video {\r\n        display: block;\r\n        width: 100%;\r\n        height: 100%;\r\n      }\r\n    }\r\n  }\r\n  &-head-wrap {\r\n    position: absolute;\r\n  }\r\n  &-side-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-modal-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-contextmenu-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-footbar {\r\n    height: $footbar-height;\r\n    background-color: white;\r\n  }\r\n  &-danmaku {\r\n    position: absolute;\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n\r\n  &-tooltip {\r\n    position: absolute;\r\n    left: 50%;\r\n    top: -30px;\r\n    transform: translateX(-50%);\r\n    white-space: nowrap;\r\n    display: none;\r\n    height: 20px;\r\n    line-height: 20px;\r\n    padding: 0 4px;\r\n    font-size: 12px;\r\n    background-color: $panel-black;\r\n    color: $text-white;\r\n    border-radius: $border-radius;\r\n  }\r\n  li {\r\n    list-style: none;\r\n  }\r\n}\r\n","// 主题变量\r\n$prefix: \"mfuns-player\";\r\n\r\n$bg-white: #ffffff; // 背景白色\r\n$bg-black: #202020; // 背景暗色\r\n\r\n$panel-white: #ffffff; // 面板白色\r\n$panel-black: #202020a0; // 面板黑色\r\n\r\n$text-black: #404040; // 文字黑色\r\n$text-grey: #808080; // 文字灰色\r\n$light-grey: #e6e6e6; // 淡灰色\r\n\r\n$text-white: #ffffffe0; // 文字白色\r\n$text-semiwhite: #ffffff80; // 文字半透明白色\r\n$light-semiwhite: #ffffff40; // 淡半透明白色\r\n\r\n$primary-color: var(--mp-primary-color, #7b7ff7); // 主要颜色\r\n$secondary-color: var(--mp-secondary-color, $primary-color); // 次要颜色\r\n\r\n$border-radius: var(--mp-border-radius, 4px); // 圆角大小\r\n\r\n$footbar-height: 40px; // 底栏高度\r\n$controller-fixed-height: 36px; // 固定控制栏高度\r\n","// 控制栏\r\n@import \"../theme.scss\";\r\n\r\n.#{$prefix} {\r\n  &-controller-wrap {\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    height: 50px;\r\n  }\r\n  &-controller-mask {\r\n    position: absolute;\r\n    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAADGCAYAAAAT+OqFAAAAdklEQVQoz42QQQ7AIAgEF/T/D+kbq/RWAlnQyyazA4aoAB4FsBSA/bFjuF1EOL7VbrIrBuusmrt4ZZORfb6ehbWdnRHEIiITaEUKa5EJqUakRSaEYBJSCY2dEstQY7AuxahwXFrvZmWl2rh4JZ07z9dLtesfNj5q0FU3A5ObbwAAAABJRU5ErkJggg==)\r\n      repeat-x bottom;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    height: 100px;\r\n    pointer-events: none;\r\n  }\r\n  &-controller {\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    height: 50px;\r\n    margin: 0 15px;\r\n    color: $text-white;\r\n    &-content {\r\n      height: calc(100% - 20px);\r\n      position: relative;\r\n      margin: 15px 0 5px;\r\n      display: flex;\r\n      justify-content: space-between;\r\n      align-items: center;\r\n    }\r\n    &-left {\r\n      height: 100%;\r\n      max-width: 200px;\r\n      display: flex;\r\n      align-items: center;\r\n    }\r\n    &-right {\r\n      height: 100%;\r\n      min-width: 200px;\r\n      display: flex;\r\n      justify-content: flex-end;\r\n      align-items: center;\r\n    }\r\n    &-progress {\r\n      width: 100%;\r\n      height: 14px;\r\n      position: absolute;\r\n      top: 0;\r\n      display: flex;\r\n      align-items: center;\r\n      cursor: pointer;\r\n      box-sizing: border-box;\r\n    }\r\n    &-button {\r\n      position: relative;\r\n      height: 30px;\r\n      font-size: 12px;\r\n      display: flex;\r\n      justify-content: center;\r\n      cursor: pointer;\r\n      .#{$prefix}-controller-icon {\r\n        font-size: 21px;\r\n        line-height: 30px;\r\n      }\r\n      &:hover {\r\n        .#{$prefix}-tooltip {\r\n          display: block;\r\n        }\r\n        .#{$prefix}-controller-panel-wrap {\r\n          display: block;\r\n        }\r\n        .#{$prefix}-controller-icon-wrap {\r\n          transform: translateY(-2px);\r\n          &:active {\r\n            transform: translateY(1px);\r\n          }\r\n        }\r\n      }\r\n      &.state-control {\r\n        .#{$prefix}-controller-panel-wrap {\r\n          display: block;\r\n        }\r\n        .#{$prefix}-controller-icon-wrap {\r\n          transform: translateY(-2px);\r\n        }\r\n      }\r\n    }\r\n    &-icon-wrap {\r\n      padding: 0 7px;\r\n      transition: transform 0.3s ease;\r\n    }\r\n    &-panel-wrap {\r\n      position: absolute;\r\n      left: 50%;\r\n      bottom: 100%;\r\n      transform: translateX(-50%);\r\n      display: none;\r\n    }\r\n    &-panel {\r\n      margin-bottom: 20px;\r\n      background-color: $panel-black;\r\n      border-radius: $border-radius;\r\n    }\r\n    &-time {\r\n      width: 90px;\r\n      text-align: center;\r\n      font-size: 13px;\r\n      margin: 0 5px;\r\n      cursor: pointer;\r\n      &-label {\r\n        width: 100%;\r\n        white-space: nowrap;\r\n        text-align: center;\r\n      }\r\n      &-input {\r\n        display: none;\r\n        width: 60px;\r\n        margin: auto;\r\n        font-size: 13px;\r\n        text-align: center;\r\n      }\r\n      &.state-input & {\r\n        &-label {\r\n          display: none;\r\n        }\r\n        &-input {\r\n          display: block;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  // 控制栏固定模式\r\n  &.mode-fixedcontroller & {\r\n    &-video-area {\r\n      width: 100%;\r\n      height: calc(100% - $controller-fixed-height);\r\n    }\r\n    &-footbar {\r\n      border-top: 1px solid $light-grey;\r\n      height: $footbar-height - 1;\r\n    }\r\n    &-controller-wrap {\r\n      position: relative;\r\n      background: white;\r\n      height: $controller-fixed-height;\r\n    }\r\n    &-controller {\r\n      height: 100%;\r\n      margin: 0;\r\n      color: $text-black;\r\n      &-mask {\r\n        display: none;\r\n      }\r\n      &-content {\r\n        height: calc(100% - 6px);\r\n        margin: 6px 0 0;\r\n      }\r\n      &-progress {\r\n        height: 14px;\r\n        padding-bottom: 2px;\r\n        top: -8px;\r\n        align-items: end;\r\n      }\r\n      &-icon-wrap {\r\n        padding: 0 5px;\r\n      }\r\n      &-panel {\r\n        background-color: $panel-white;\r\n      }\r\n      .#{$prefix} {\r\n        &-slider {\r\n          &-track {\r\n            background: $light-grey;\r\n          }\r\n        }\r\n        &-tooltip {\r\n          background-color: $panel-white;\r\n          color: $text-black;\r\n        }\r\n        &-checkbox {\r\n          &-icon {\r\n            border-color: $text-grey;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","@import \"../theme.scss\";\r\n\r\n.#{$prefix} {\r\n  &-progress-bar {\r\n    position: relative;\r\n    width: 100%;\r\n    height: 4px;\r\n    background-color: $light-semiwhite;\r\n    transition: height 0.2s ease;\r\n    &-played {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      height: 100%;\r\n      background-color: $primary-color;\r\n    }\r\n    &-buffered {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      height: 100%;\r\n      background-color: $text-semiwhite;\r\n    }\r\n    &-thumb-track {\r\n      position: absolute;\r\n      top: 50%;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 0;\r\n    }\r\n    &-thumb {\r\n      position: absolute;\r\n      transform: translate(-50%, -50%) scale(0);\r\n      width: 14px;\r\n      height: 14px;\r\n      background-color: $primary-color;\r\n      border-radius: 7px;\r\n      transition: transform, border;\r\n      transition-timing-function: ease;\r\n      transition-duration: 0.2s;\r\n      box-sizing: border-box;\r\n      border: 4px solid white;\r\n    }\r\n    &-preview {\r\n      position: absolute;\r\n      top: -12px;\r\n      width: 0;\r\n      height: 0;\r\n    }\r\n    &-time {\r\n      position: absolute;\r\n      left: 50%;\r\n      bottom: 0px;\r\n      transform: translateX(-50%);\r\n      display: none;\r\n      height: 20px;\r\n      line-height: 20px;\r\n      padding: 0 4px;\r\n      font-size: 12px;\r\n      background-color: $panel-black;\r\n      color: $text-white;\r\n      border-radius: $border-radius;\r\n    }\r\n    &-tip {\r\n      display: none;\r\n      position: absolute;\r\n      top: -10px;\r\n      &::after {\r\n        content: \"\";\r\n        display: block;\r\n        position: absolute;\r\n        bottom: -10px;\r\n        left: 50%;\r\n        transform: translateX(-50%);\r\n        border: 5px solid;\r\n        border-color: $primary-color transparent transparent transparent;\r\n      }\r\n    }\r\n    &.#{$prefix}-progress-bar-active & {\r\n      height: 6px;\r\n      &-thumb {\r\n        transform: translate(-50%, -50%) scale(1);\r\n      }\r\n      &-tip {\r\n        display: block;\r\n      }\r\n      &-time {\r\n        display: inline-block;\r\n      }\r\n    }\r\n    &.#{$prefix}-progress-bar-dragging {\r\n      .#{$prefix}-progress-bar-thumb {\r\n        border-width: 2px;\r\n      }\r\n    }\r\n  }\r\n  // 控制栏固定模式\r\n  &.mode-fixedcontroller & {\r\n    &-progress-bar {\r\n      background-color: rgba($light-grey, 0.5);\r\n      &-buffered {\r\n        background-color: $primary-color;\r\n        opacity: 0.5;\r\n      }\r\n      &-time {\r\n        background-color: $panel-white;\r\n        color: $text-black;\r\n      }\r\n    }\r\n  }\r\n}\r\n","@import \"../theme.scss\";\r\n\r\n.#{$prefix} {\r\n  &-side-wrap {\r\n    display: none;\r\n    &.state-show {\r\n      display: block;\r\n    }\r\n  }\r\n  &-side {\r\n    position: absolute;\r\n    right: 20px;\r\n    width: 300px;\r\n    top: 50px;\r\n    height: calc(100% - 120px);\r\n    background-color: $panel-black;\r\n    border-radius: $border-radius;\r\n    color: white;\r\n    &-mask {\r\n      position: absolute;\r\n      width: 1